.Entete {
  position: sticky;
  top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #444;
  color: white;

  h1 {
    font-size: 1.4rem;
    padding: 1rem 2rem;
  }

  nav {
    display: flex;
